    //#region General
    getVersion(){
        return version;
    }

    getRenderVersion(viewerUUID:any){
        let viewer = viewerUUID ? this.viewerMap.get(viewerUUID) : this.viewerMap.get(this.defaultViewerID);
        if(viewer){
            return viewer.getRenderVersion();   
        }
        return "Invalid viewer ID";   
    }

    createViewer(_containerID:any, disableMouseEvents:boolean = false){
        let viewerUUID = Utility.create_UUID();
        let viewer = new Viewer(viewerUUID, _containerID, this.eventDispacther, disableMouseEvents );     
        if(this.defaultViewerID === null){
            this.defaultViewerID = viewerUUID;
        }
        this.viewerMap.set(viewerUUID, viewer);

        /*
        boundStore.setState(state => {
            state.viewerState.createViewer(viewerUUID);
        })
        boundStore.setState(state => state.viewerState.)
        */
        return viewerUUID;  
    }

    getDefaultViewerID(){
        return this.defaultViewerID;
    }

    getEventsList(){
        return { viewerEvents, globalEvents };
    }

    getEventDispatcher(){
        if(this.eventDispacther){
            return this.eventDispacther;
        }
        return null;   
    }   

    deleteViewer(viewerUUID:any){
        let viewer = viewerUUID ? this.viewerMap.get(viewerUUID) : this.viewerMap.get(this.defaultViewerID);
        if(viewer){
            viewer.clear();
            return viewer.delete();   
        }
        return "Invalid viewer ID";    
    } 
       
    loadModel(api:string, url:string, viewerUUID:any){
        let viewer = viewerUUID ? this.viewerMap.get(viewerUUID) : this.viewerMap.get(this.defaultViewerID);
        if(viewer){
            return viewer.loadModel(api, url);  
        }
        return Promise.reject("Invalid viewer ID");    
    }   

    getModelInfo(viewerUUID:any){
        let viewer = viewerUUID ? this.viewerMap.get(viewerUUID) : this.viewerMap.get(this.defaultViewerID);
        if(viewer){
            return viewer.getModelInfo();
        }
        return "Invalid viewer ID";   
    }

    getSceneBoundingBox(onlyVisible:boolean=true, viewerUUID:any) {
        let viewer = viewerUUID ? this.viewerMap.get(viewerUUID) : this.viewerMap.get(this.defaultViewerID);
        if(viewer){
            return viewer.getSceneBoundingBox(onlyVisible);
        }
        return "Invalid viewer ID";  
    }
    showModel(viewerUUID:any){
        let viewer = this.getViewer(viewerUUID);
        if(viewer){
            return viewer.showModel(); 
        }
        return Promise.reject("Invalid viewer ID");
    }

    getViewer(viewerUUID:any):Viewer{
        return viewerUUID ? this.viewerMap.get(viewerUUID) : this.viewerMap.get(this.defaultViewerID)
    }

    setExternalLogger(externalLogger:any)
    {
        logger.setExternalLogger(externalLogger);
    }
    setInterationMode(mode:InteractionMode, viewerUUID:any) {
        let viewer = this.getViewer(viewerUUID);
        viewer.setInteractionMode(mode);
    }
    setBackground(viewerUUID:any,type:BackgroundType,data:Color|any) {
        let viewer = this.getViewer(viewerUUID);
        viewer.setBackground(type,data);
    }
    getViewerSize(viewerUUID:any) : [number, number]{ 
        let viewer = this.getViewer(viewerUUID);
        return viewer.getViewerSize();
    }
    //#endregion
    
    //#region Camera
    getCameraStdViews(viewerUUID:any):StdView[]{
        let viewer = viewerUUID ? this.viewerMap.get(viewerUUID) : this.viewerMap.get(this.defaultViewerID);
        return viewer.getCameraStdViews();
    }
    getCameraInfo(camType:any , viewerUUID:any):any{
        let viewer = viewerUUID ? this.viewerMap.get(viewerUUID) : this.viewerMap.get(this.defaultViewerID);
        return viewer.getCameraInfo(camType);
    }
    setCameraInfo(camData:any, viewerUUID:any){
        let viewer = viewerUUID ? this.viewerMap.get(viewerUUID) : this.viewerMap.get(this.defaultViewerID);
        return viewer.setCameraInfo(camData);
    }
    setCameraProjection(camType:CameraType, viewerUUID:any) {
        let viewer = viewerUUID ? this.viewerMap.get(viewerUUID) : this.viewerMap.get(this.defaultViewerID);
        viewer.setCameraProjection(camType);
    }
