import { SpeedToStructuredString } from "./connector/Contants";
import RequestMeter from "./connector/RequestMeter";
import { EventDispatcher } from "./EventDispatcher";
import { internalEvents, viewerEvents } from "./Events";
import { ManagedURLDownloader } from "./ProgressiveLoader";
import { Utility } from "./Utility";

type DownloadID = string;
type DownloadItemGroupID = DownloadID;

type DownloadItem = {
    id: string,
    loaded: number,
    total: number
}
type DownloadItemGroup = {
    id: DownloadItemGroupID,
    info: Info,
    parts: DownloadItem[]
}

type Info = {
    title: string,
    notify:boolean,
    totalSize: number,
    msg ?: string,
    tag ?: string
}

type AxiosEvent = {downloadId:string, partId:string, event: any};

class NetworkManager {
    private viewerId: string
    private dispatcher: EventDispatcher
    private downloads: Map<DownloadItemGroupID, DownloadItemGroup>
    constructor(eventDispatcher: EventDispatcher, viewerId: string) {
        this.viewerId = viewerId;
        this.dispatcher = eventDispatcher
        this.downloads = new Map()
        this.register_events();
    }
    register_events(){
        this.dispatcher.addEventListener(internalEvents.NETWORK_ADD_PART, this.handleAddPart.bind(this));
        this.dispatcher.addEventListener(internalEvents.NETWORK_UPDATE_PART, this.handleUpdataPart.bind(this));
    }
    createGroup(info:Info): DownloadItemGroupID {
        let id = Utility.create_UUID();
        this.downloads.set(id,{
            id,
            info,
            parts: [] 
        });
        if(info.notify)
        this.dispatcher.dispatchEvent({
            data: {id, event:info},
            type: viewerEvents.DOWNLOAD_START,
            viewerID: this.viewerId
        })
        ManagedURLDownloader.createURLGroup(id);
        //console.log("group created",info);
        return id;
    }
    private handleAddPart(event:any) {
        let data = event.data as AxiosEvent;
        let id = data.downloadId;
        let axios_event = data.event;
        this.addPart(id,{
            id: data.partId,
            loaded: 0,
            total: 0,
        })
    }
    private handleUpdataPart(event:any) {
        let data = event.data as AxiosEvent;
        let id = data.downloadId;
        let axios_event = data.event;
        this.updatePart(id,data.partId, axios_event);
    }
    addPart(groupId:DownloadItemGroupID, item: DownloadItem) : boolean {
        let group = this.downloads.get(groupId);
        if(group) {
           group.parts.push(item);
           return true
        }
        return false
    }
    updatePart(groupId:DownloadItemGroupID, partId:string,event:any): boolean {
        let group = this.downloads.get(groupId);
        if(group) {
            let part = group.parts.find(e => e.id === partId);
            if(part) {
                part.loaded =event.loaded;
                part.total = event.total;
                //console.log("update part",part.id,part.loaded,part.total)
            }
            let total = group.info.totalSize;
            let loaded = 0;
            group.parts.forEach(e => {
                loaded += e.loaded;
            })
            if(group.info.notify)
            this.dispatcher.dispatchEvent({
                data: {id:groupId,event: {loaded,total}},
                type: viewerEvents.DOWNLOAD_PROGRESS,
                viewerID: this.viewerId
            })
            //console.log("update total",loaded,total)
            
         }
         return false
    }
    deleteGroup(id: DownloadItemGroupID):boolean {
        if(this.downloads.has(id)) {
            let group = this.downloads.get(id);

            if(group.info.notify)
            this.dispatcher.dispatchEvent({
                //data: {id,event: {title: group.info.title}},
                data: {id, event : group.info},
                type: viewerEvents.DOWNLOAD_END,
                viewerID: this.viewerId
            })
            ManagedURLDownloader.deleteURLGroup(id);
            this.downloads.delete(id);
            //console.log("group deleted",id);
            return true;
        }
        return false;
    }
}

export default NetworkManager;