    //#region probing
    probeFromNodes(pointerData:{xyFromTop:number[], width:number,height:number}){
        let mcaxFilter = new  MCAXFilter(this.renderApp,this.mcax);
        let nodeIds = this.renderApp.getVisibleNodeIds();
        let visible = mcaxFilter.getVisibleGeometries(nodeIds);
        let selection:any[] = [];
        visible.forEach((geo)=> {
            geo.representations.forEach((repId:string) => {
                if(mcaxFilter.getRepresentationType(repId) !== "BBOX")
                selection.push(repId);
            })
        })
        let data = this.renderApp.probeFromNodes(selection,pointerData);
        return data;
        //console.log(data);
    }
