    add3dLabelforNodeId(id:string, type:Label3DType, nodeIds:Array<number>, modelIndex : number) : boolean{     
    
        let mcaxFilter = new  MCAXFilter(this.renderApp,this.mcax);
        let partNodeIds = this.renderApp.getVisibleNodeIds();
        let visible = mcaxFilter.getVisibleGeometries(partNodeIds);
        let selectedPartNodes:any[] = [];
        visible.forEach((geo)=> {
            geo.representations.forEach((repId:string) => {
                if(mcaxFilter.getRepresentationType(repId) !== "BBOX")
                selectedPartNodes.push(repId);
            })
        });

        let results :Array<boolean> = [];
        if(selectedPartNodes.length > 0){
            nodeIds.forEach((nodeId, index) => {
                let nodeidIndex = this.caeResult.getNodeIdIndex(nodeId, modelIndex);
                let result = this.labelManager.add3dLabelforNodeId(id + "_" + index, type, nodeId, modelIndex, nodeidIndex, selectedPartNodes);
                results.push(result);
            });
            if(results.length === nodeIds.length && !results.includes(false))
                return true;
        }               
        return false;
    } 

    add3dLabelforNodeIndex(id:string, type:Label3DType, nodeindex:number, modelIndex : number) : string {     
    
        let mcaxFilter = new  MCAXFilter(this.renderApp,this.mcax);
        let partNodeIds = this.renderApp.getVisibleNodeIds();
        let visible = mcaxFilter.getVisibleGeometries(partNodeIds);
        let selectedPartNodes:any[] = [];
        visible.forEach((geo)=> {
            geo.representations.forEach((repId:string) => {
                if(mcaxFilter.getRepresentationType(repId) !== "BBOX")
                selectedPartNodes.push(repId);
            })
        });

        if(selectedPartNodes.length > 0){
            let nodeIds=  this.caeResult.getNodeId(nodeindex);
            let labelId = id;
            let result = this.labelManager.add3dLabelforNodeId(labelId, type, nodeIds, modelIndex, nodeindex, selectedPartNodes);
            if(result === true)
                return labelId; 
        }               
        return null;
    }

