import { PointerData } from "./rendererTypes";

class Utility {
     
    static create_UUID():string{
        let dt = new Date().getTime();
        let uuid = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
            let r = (dt + Math.random()*16)%16 | 0;
            dt = Math.floor(dt/16);
            return (c=='x' ? r :(r&0x3|0x8)).toString(16);
        });
        return uuid;
    } 
     
    static getURLParameterByName = function (URL:string, name:string):string {
        name = name.replace(/[\[]/, "\\[").replace(/[\]]/, "\\]");
        var regex = new RegExp("[\\?&]" + name + "=([^&#]*)",'i'),
            results = regex.exec(URL);
        return results === null ? "" : decodeURIComponent(results[1].replace(/\+/g, " "));
    }

    static getPathFromUrl = function (url:string) {
        return url.split(/[?#]/)[0];
      }

    static deepCopy(obj:any):any {
        if(typeof obj !== 'object' || obj === null) {
            return obj;
        }
      
        if(obj instanceof Array) {
            return obj.reduce((arr, item, i) => {
                arr[i] = this.deepCopy(item);
                return arr;
            }, []);
        }
    
        if(obj instanceof Object) {
            return Object.keys(obj).reduce((newObj:any, key) => {
                newObj[key] = this.deepCopy(obj[key]);
                return newObj;
            }, {})
        }
    }

    static downloadDataAsFile(data:any, filename:any, type:any){
        if(!type)
            type = 'application/json';
        // @ts-ignore
        const blob:any = new Blob([JSON.stringify(data)], { type: type });
        // @ts-ignore
        const url = URL.createObjectURL(blob);
        // Create a new anchor element
        // @ts-ignore
        const a = document.createElement('a');
        a.href = url;
        a.download = filename || 'download';
        a.click();
        a.remove();
    }

    static getProbeInputData(event:any):PointerData {
        let e = event.data;
            let container = e.target;
            let rect = container.getBoundingClientRect();
            let pointerData = {
                xyFromTop: [ e.clientX - rect.left,
                e.clientY - rect.top],
                width: rect.width,
                height: rect.height
            }
        return pointerData;
    }

    static getDOMSize(containerID : string) : [number, number]{
        let size :  [number, number] = [0,0];
        const DOM = document.getElementById(containerID); 
        if(DOM){
            size[0] = DOM.clientWidth;
            size[1] = DOM.clientHeight;
        }  
        return size;
    }
}

export { Utility }
