import { Logger as logger } from '../Logger';
import * as glmatrix from "gl-matrix";
import { InteractionMode } from '../AppContants';
import { AppState } from '../AppState';
import {Utility} from '../Utility'
import { EventDispatcher } from '../EventDispatcher';
import { viewerEvents } from '../Events';
import vctViewer from "../../plugins/main";

class GUIState{
    public planeOptions:Map<PlaneId,PlaneGUIState>;
    constructor(){
        this.planeOptions = new Map<PlaneId,PlaneGUIState>();
    }
}

export enum SelectionMode{
    NONE = 0,
    THREE_PT = 1,
    FACE = 2
}

class PlaneGUIState{
    public isPlaneEnabled:boolean;
    public isPlaneVisible:boolean;
    public sliderMinMax: [number,number];
    public primarySliderValue:number;
    public rotSliderUValue:number;
    public rotSliderVValue:number;
    public rotSliderNValue:number;
    public delta:number;
    public deltaRotU:number;
    public deltaRotV:number;
    public deltaRotN:number;
    public transform: glmatrix.mat4;
    public initTransform: glmatrix.mat4;
    constructor(){
        this.isPlaneEnabled = false;
        this.isPlaneVisible = false;
        this.sliderMinMax = [0,0];
        this.primarySliderValue = 0;
        this.rotSliderUValue = 360;
        this.rotSliderVValue = 360;
        this.rotSliderNValue = 360;
        this.delta = 0;
        this.deltaRotU = 0;
        this.deltaRotV = 0;
        this.deltaRotN = 0;
        this.transform = glmatrix.mat4.create();
        this.initTransform = glmatrix.mat4.create();
    }
}

export type PlaneId = number | string;
export type Color = [number,number,number,number];
class Section
{
    private viewerId:any;
    private renderApp:vctViewer;
    private appState:AppState;
    private activePlaneId:PlaneId;
    private externalEvents:any;
    private externalEventDispatcher:any;
    private eventDispatcher:EventDispatcher;
    private bbox:any;
    private guiState:GUIState;
    private selectedPts:any[];
    constructor(viewerId:any,_renderApp:vctViewer, appState:AppState, eventDispatcher:EventDispatcher)
    {
        this.renderApp = _renderApp;
        this.appState = appState;
        this.activePlaneId = -1;
        this.externalEvents = this.renderApp.getEvents();
        this.externalEventDispatcher = this.renderApp.getEventDispatcher();
        this.eventDispatcher = eventDispatcher;
        this.registerEvents();
        this.bbox = null
        this.guiState = new GUIState();
        this.selectedPts = [];
        this.viewerId = viewerId;
    }

    registerEvents(){
        this.externalEventDispatcher.addEventListener(this.externalEvents.PROBE_FINISH,this.handleSelection.bind(this));
        this.externalEventDispatcher.addEventListener(this.externalEvents.MODEL_LOADED,this.handleOnModelLoad.bind(this));
    }

    handle3ptSelect(probeData:any){
        if(this.selectedPts.length < 2)
        {
            this.selectedPts.push(probeData.hitPoint);
        }
        else
        {
            this.selectedPts.push(probeData.hitPoint);
            let a= glmatrix.vec3.fromValues(this.selectedPts[0][0],this.selectedPts[0][1],this.selectedPts[0][2]);
            let b= glmatrix.vec3.fromValues(this.selectedPts[1][0],this.selectedPts[1][1],this.selectedPts[1][2]);
            let c= glmatrix.vec3.fromValues(this.selectedPts[2][0],this.selectedPts[2][1],this.selectedPts[2][2]);
            let d1 = glmatrix.vec3.sqrDist(a,b);
            let d2 = glmatrix.vec3.sqrDist(b,c);
            let d3 = glmatrix.vec3.sqrDist(c,a);
            let e = this.appState.EPSIOLON;
            if(d1 > e && d2 > e && d3 > e){
                this.eventDispatcher.dispatchEvent({
                    type: viewerEvents.SECTION_PLANE_SELECTED,
                    viewerID: this.viewerId,
                    data: 
                    { points: Array.from(this.selectedPts), planeId: this.activePlaneId}
                });
                //console.error("event dispatched");
                this.selectedPts = [];
            }
            this.selectedPts = [];
        }
        logger.setStatusBar("Probing from 3pt section");
    }
    handleFaceSelect(probeData:any){

        if(probeData.primitiveSize == 3)
        {
            const data = probeData.primitiveData;
            this.selectedPts = [
                [data[0],data[1],data[2]],
                [data[3],data[4],data[5]],
                [data[6],data[7],data[8]]
            ];
            let a= glmatrix.vec3.fromValues(data[0],data[1],data[2]);
            let b= glmatrix.vec3.fromValues(data[3],data[4],data[5]);
            let c= glmatrix.vec3.fromValues(data[6],data[7],data[8]);
            let d1 = glmatrix.vec3.sqrDist(a,b);
            let d2 = glmatrix.vec3.sqrDist(b,c);
            let d3 = glmatrix.vec3.sqrDist(c,a);
            let e = this.appState.EPSIOLON;
            if(d1 > e && d2 > e && d3 > e){
                this.eventDispatcher.dispatchEvent({
                    type: viewerEvents.SECTION_PLANE_SELECTED,
                    viewerID: this.viewerId,
                    data: 
                    { points: Array.from(this.selectedPts), planeId: this.activePlaneId}
                });
                //this.renderApp.addPoint(Utility.create_UUID(),glmatrix.vec3.create(),1,[1,0,0,1]);
                this.selectedPts = [];
            }
            
            logger.setStatusBar("Probing from face section");
        }
        else{
            logger.setStatusBar("Please select a face");
        }
        
    }
    handleSelection(e:any){
        let probeData = e.data;

        if(probeData?.hitPoint)
        {
            if(this.appState.interactionMode == InteractionMode.SECTION_3PT)
            this.handle3ptSelect(probeData);
            else if(this.appState.interactionMode == InteractionMode.SECTION_FACE)
            this.handleFaceSelect(probeData);
        }
    }
    handleOnModelLoad(e:any){
        this.bbox = this.renderApp.getSceneBoundingBox(false);
    }
    getSectionGUIData(){
        return Utility.deepCopy(this.guiState);
    }

    setSectionGUIData(guiState:GUIState){
        this.guiState = guiState;
    }
    addSectionPlane(planeId:PlaneId, transform:glmatrix.mat4, color: Color) {
        let newPlaneState = new PlaneGUIState();
        newPlaneState.transform = glmatrix.mat4.clone(transform);
        newPlaneState.initTransform = glmatrix.mat4.clone(transform);
        this.guiState.planeOptions.set(planeId,newPlaneState);
        this.renderApp.addSectionPlane(planeId,transform,color);
    }
    deleteSectionPlane(planeId:PlaneId) {
        this.guiState.planeOptions.delete(planeId);
        this.renderApp.deleteSectionPlane(planeId);
    }
    setSectionPlaneEquation(planeId:PlaneId, transform:glmatrix.mat4, initTransform?:glmatrix.mat4){
        let curPlane = this.guiState.planeOptions.get(planeId);
        if(curPlane){
            curPlane.transform = glmatrix.mat4.clone(transform);
            curPlane.initTransform = initTransform ? glmatrix.mat4.clone(initTransform): null;
        }
        this.renderApp.setSectionPlaneEquation(planeId,transform,initTransform);
    }
    
    getSectionPlaneEquation(planeId:PlaneId) {
        let curPlane = this.guiState.planeOptions.get(planeId);
        const {transform, initTransform} = this.renderApp.getSectionPlaneEquation(planeId);
        if(curPlane) {
            curPlane.transform = glmatrix.mat4.clone(transform);
            curPlane.initTransform = initTransform ? glmatrix.mat4.clone(initTransform) : null;
        }
        return {transform, initTransform};
    }
    setActive(planeId:PlaneId) {
        this.activePlaneId = planeId;
        this.renderApp.setActiveSectionPlaneId(planeId);
    }
    setSelection(mode:SelectionMode){
        switch (mode) {
            case SelectionMode.FACE:
                this.appState.interactionMode = InteractionMode.SECTION_FACE;
                break;
            case SelectionMode.THREE_PT:
                this.appState.interactionMode = InteractionMode.SECTION_3PT;
                break;
            default:
                this.appState.interactionMode = InteractionMode.DEFAULT;
                break;
        }
    }
    setPlaneState(planeId:PlaneId, params:any){

        this.setSelection(params.selectionMode);

        if(params.isPlaneEnabled)
        {
            this.renderApp.enableClipPlane(planeId)
        }
        else{
            this.renderApp.disableClipPlane(planeId)
        }

        if(params.isPlaneVisible)
        {
            this.renderApp.showClipPlane(planeId)
        }
        else{
            this.renderApp.hideClipPlane(planeId)
        }
        if(this.guiState.planeOptions.has(planeId))
        {
            this.guiState.planeOptions.set(planeId, params);
        }
        
    }
    invert(planeId:PlaneId){
        // let currentEqn:{eqn:number[],transform:glmatrix.mat4} = this.getSectionPlaneEquation(planeId);
        // if(currentEqn == undefined)
        // return;
        // let {eqn,transform} = currentEqn;
        // const invertedAxes = glmatrix.mat4.clone(transform);
        // invertedAxes[8] = -invertedAxes[8]; invertedAxes[9] = -invertedAxes[9]; invertedAxes[10] = -invertedAxes[10];
        // const invNormal = [-eqn[0],-eqn[1],-eqn[2]];
        // this.setSectionPlaneEquation(planeId,[...invNormal ,-eqn[3]] as [number,number,number,number],invertedAxes);
    }
    planeFrom3pts(planeId:PlaneId,p1:glmatrix.vec3,p2:glmatrix.vec3,p3:glmatrix.vec3,transform:glmatrix.mat4){
        const kEdge1 = glmatrix.vec3.create();
        const kEdge2 = glmatrix.vec3.create(); 
        glmatrix.vec3.sub(kEdge1,p2,p1)
        glmatrix.vec3.sub(kEdge2,p3,p1)
		const n = glmatrix.vec3.create();
		glmatrix.vec3.cross(n,kEdge1,kEdge2);
        glmatrix.vec3.normalize(n,n);
        let d = -glmatrix.vec3.dot(n,p1);
        if(glmatrix.vec3.sqrLen(n) > 0.0001){
            const eqn = [n[0],n[1],n[2] ,d];
            this.setSectionPlaneEquation(planeId,transform);
            //console.log('plane eqn from 3pts',eqn);
        }
    }
    translatePlane(delta:number,deltaSlice:number,planeId:number){
        if(delta == 0 && deltaSlice == 0)
        return;
        //this.slicePlaneOffset[planeId] +=deltaSlice;
        //this.renderApp.translateSectionPlane(delta,deltaSlice,planeId);
        // let currentEqn = this.getSectionPlaneEquation(planeId);
        // if(currentEqn == undefined)
        // return;
        // currentEqn[3] += delta;
        // this.renderApp.setSlicePlaneOffset(this.slicePlaneOffset[planeId],planeId);
        // this.setSectionPlaneEquation(planeId,currentEqn)

    }
    rotatePlane(deltaU:number,deltaV:number,deltaN:number,planeId:number){
        if(deltaU == 0 && deltaV == 0 && deltaN == 0)
        return;
        //this.renderApp.rotateSectionPlane(deltaU,deltaV,deltaN,planeId);
    }
    setBounds(planeId:PlaneId){
        let planeState = this.guiState.planeOptions.get(planeId)
        if(planeState)
        {
            planeState.sliderMinMax = [-this.bbox.getRadius(),this.bbox.getRadius()];
            this.guiState.planeOptions.set(planeId,planeState);
        }
    }
}
export { Section };