type URLDataObject = {
    data : Promise<ArrayBuffer | string>;
    timestamp : Date;
    isValid : boolean;
    size : number;
    type : ArrayBuffer | Object
}


export class NetworkCache {
    private cacheURL = new Map<string, URLDataObject>();
    private viewerId: string
    constructor (viewerId:string) {
        this.viewerId = viewerId
    }

    get(url : string) : Promise<ArrayBuffer | string | null> {
        const dataObject = this.cacheURL.get(url);
        if(dataObject && dataObject.isValid)
            return dataObject.data;
        return null;
    }

    add(url : string, data : Promise<ArrayBuffer | string>) : boolean{
        let dataObject : URLDataObject = {
            data : data,
            timestamp : new Date(),
            size : 0,
            isValid :false,
            type : ArrayBuffer
        };
 
        dataObject.data
        .then((response : ArrayBuffer | string) =>{
            if(response instanceof ArrayBuffer)            
                dataObject.size = response.byteLength;
            else{
                dataObject.size = JSON.stringify(response).length;
                dataObject.type = Object;
            }              
           dataObject.isValid = true;
        })
        .catch((error) => {
            console.log(error);
            dataObject.size = 0;
            dataObject.isValid = false;
        })
        this.cacheURL.set(url, dataObject);
        return true;
    }

    remove(url : string) : boolean{
        this.cacheURL.delete(url);
        return true;
    }

    removeAll() : boolean{
        this.cacheURL.clear();
        return true;
    }
}