import { Logger as logger } from '../Logger';
import { InteractionMode } from '../AppContants.js';
import { AppState } from '../AppState';
import vctViewer from '../../plugins/main';
//import { EventDispatcher } from '../../plugins/EventDispatcher';
import { EventDispatcher } from  '../rendererTypes';
import { Viewer } from '../Viewer';
import { Utility} from '../Utility';
import { viewerEvents } from '../Events';
import { vec3 } from 'gl-matrix';
import { Label3DType, IProbeData } from '../rendererTypes';
class LabelManager
{
    private renderApp:vctViewer;
    private viewer: Viewer;
    private appState:AppState;
    private events:any;
    private externalEventDispatcher:EventDispatcher;
    private hitPoints:{id:string,hit:vec3}[];

    constructor(_renderApp:any, appState:AppState, viewer:Viewer)
    {
        this.renderApp = _renderApp;
        this.hitPoints = [];
        this.appState = appState;
        this.events = this.renderApp.getEvents();
        this.viewer = viewer;
        this.externalEventDispatcher = this.renderApp.getEventDispatcher();
        this.registerEventHandlers();
    }
    registerEventHandlers(){
        this.externalEventDispatcher.addEventListener(this.events.CLICK,this.handleClick.bind(this));
    }
    handleClick(event:any) {
        switch (this.appState.interactionMode) {
            case InteractionMode.LABEL3D_POINT:
                this.handleProbeLabel(event,Label3DType.PROBE);
                break;
            case InteractionMode.LABEL3D_FACE:
                this.handleProbeLabel(event,Label3DType.FACE)
                break;
            case InteractionMode.LABEL_MEASUREMENT_POINT_TO_POINT:
                this.handleMeasurementP2P(event);
                break;
            case InteractionMode.LABEL_MEASUREMENT_3PT_ARC:
                this.handleMeasurementArc(event);
            default:
                break;
        }
        
    }
    handleProbeLabel(event:any,type:Label3DType) {
        let pointerData = Utility.getProbeInputData(event);
        let data = this.viewer.probeFromNodes(pointerData);
        if(data){
            //let hit = data.hitPoint;
            let hit = data.nearPoint;
            let point = vec3.fromValues(hit[0],hit[1],hit[2]);
            let id = this.renderApp.addPoint('t',point,1,[1,0,0,1]);
            this.hitPoints.push({id,hit:point});
            if(this.hitPoints.length === 1){
                this.renderApp.addLabel3D("label_probe_"+id,this.hitPoints.map(e => e.hit),type,data);
                this.hitPoints.forEach(pt => {
                    this.renderApp.removePoint(pt.id);
                })
                this.hitPoints = []
            }
        }
    }
    
    handleMeasurementP2P(event:any){
        let pointerData = Utility.getProbeInputData(event);
        let data = this.viewer.probeFromNodes(pointerData);
        if(data){
            let hit = data.hitPoint;
            let point = vec3.fromValues(hit[0],hit[1],hit[2]);
            let id = this.renderApp.addPoint('t',point,1,[1,0,0,1]);
            this.hitPoints.push({id,hit:point});
            if(this.hitPoints.length === 2){
                this.renderApp.addLabel3D("label_distance_"+id,this.hitPoints.map(e => e.hit),Label3DType.DISTANCE,data);
                this.hitPoints.forEach(pt => {
                    this.renderApp.removePoint(pt.id);
                })
                this.hitPoints = []
            }
        }
    }

    handleMeasurementArc(event:any) {
        let pointerData = Utility.getProbeInputData(event);
        let data = this.viewer.probeFromNodes(pointerData);
        if(data){
            let hit = data.hitPoint;
            let point = vec3.fromValues(hit[0],hit[1],hit[2]);
            let id = this.renderApp.addPoint('t',point,1,[1,0,0,1]);
            this.hitPoints.push({id,hit:point});
            if(this.hitPoints.length === 3){
                this.renderApp.addLabel3D("label_arc_"+id,this.hitPoints.map(e => e.hit),Label3DType.ARC,data);
                this.hitPoints.forEach(pt => {
                    this.renderApp.removePoint(pt.id);
                })
                this.hitPoints = []
            }
        }
    }
    
    add3DLabel(id:string,hitPoint:any[],type:Label3DType, probeData:IProbeData) {
        this.renderApp.addLabel3D(id,[hitPoint],type,probeData);
    }

    addMeasurementLabel(id:string,hitPoint:any[],type:Label3DType, probeData:IProbeData) {
        this.renderApp.addLabel3D(id,hitPoint,type,probeData);
    }
    showHideLabelVisibility(id:string, flag:boolean) {
        this.renderApp.showHideLabelVisibility(id,flag);
    }

    add3dLabelforNodeId(id:string, type:Label3DType, nodeid:number, modelIndex : number, nodeidIndex : number, selectedPartNodes : Array<number>) : boolean{
        return this.renderApp.add3dLabelforNodeId(id, type, nodeid, modelIndex, nodeidIndex, selectedPartNodes);
    } 
    delete3DLabel(id:string):boolean {
        return this.renderApp.deleteLabel3D(id);
    }
    get3DLabelCanvasPos(id:string) :number[] | null {
        return this.renderApp.get3DLabelCanvasPos(id);
    }
    getLabel3DInfo(id:string) : unknown | null {
       return this.renderApp.getLabel3DInfo(id);
    }

}
export { LabelManager };